body {
	font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
	font-size: 14px;
	color: #333;
	margin: 0;
	padding: 0;
}

.hover-tooltip {
	position: absolute;
	color: #fff;
	padding: 10px;
	background: rgba(0,0,0,0.8);
	transform: translate3d(-50%, -50%, 0);
	border-radius: 5px;
	pointer-events: none;
	z-index: 1000;
}

a {
	color: #08c;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

.container {
	margin-left: auto;
	margin-right: auto;
	padding: 1em;
}

.grid {
	margin-top: 50p;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.presenter {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2% 5%;
}

.source {
	margin: 2% 5%;
	width: 350px;
}

.footer {
	margin-top: 50px;
	border-top: 1px solid #eee;
	padding: 20px 0;
	font-size: 12px;
	color: #999;
}

h1, h2, h3, h4, h5, h6 {
	color: #222;
	font-weight: 100;
	margin: 0.5em 0;
}

pre.message {
	white-space: normal;
	width: 600px;
	text-align: center;
}

label {
	color: #999;
	display: inline-block;
	font-size: 0.85em;
	font-weight: bold;
	margin: 1em 0;
	text-transform: uppercase;
}

.hint {
	margin: 15px 0;
	font-style: italic;
	color: #999;
}

.comment-container {
	width: 300px;
	margin-top: 100px;
}

.counter {
	display: inline;
	margin-top: 0;
	margin-bottom: 0;
	margin-right: 10px;
}
.posts {
	clear: both;
	list-style: none;
	padding-left: 0;
	width: 100%;
	text-align: left;
}

.commentBox{
	margin-top: 40px;
	padding: 0px 0px 20px 0px;
  }
.commentPic{
	max-width: 90px;
	width: 100%;
	height: 100%;
	margin-right: 15px;
	border-radius: 10px;
  }
.comment{
	display: flex;
    padding: 10px;
    margin: 20px 0 20px 0;
  }
  .commentAuthor{
	font-size: 10px;
  }
  .commentPic{
	max-width: 45px;
	max-height: 45px;
  }
  .commentHeader{
	display: flex;
	align-items: center;
  }
  .commentBody{
	max-width: 580px;
	overflow-wrap: break-word;
  }
  .publishDate{
	color: #787878;
	margin-left: 15px;
  }